<template>
  <div class="travel-map">
    <div id="main" ref="main">

    </div>
  </div>
</template>

<script>
import geoJson from '@/assets/data.json'
import { travel } from "@/api/api";
export default {
  data() {
    return {
      points: [],
      linesData: []
    }
  },

  created() {
    travel().then(res => {
      if(res.data.status === 200){
        this.points = res.data.data.points
        this.linesData = res.data.data.linesData
        this.draw()
      }
    }).catch(err => {
      console.log(err)
    })
  },

  methods: {

    draw() {
      let myChart = this.$echarts.init(this.$refs['main'])
      this.$echarts.registerMap('china', geoJson)   //注册可用的地图，必须包含geo组件或者map图表类型的时候才可以使用
      let option = {
        backgroundColor: 'rgba(121,145,209,0.5)',
        
        geo: {
          map: 'china',
          aspectScale: 0.75,  //地图缩放长宽比
          zoom: 1.1,
          itemStyle: {
            normal: {
              // borderWidth:1,
              areaColor: {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.8,
                colorStops: [{
                  offset: 0,
                  color: '#e2e1e4'    //0%处颜色
                }, 
                {
                  offset: 1,
                  color: '#8076a3'    //100%处颜色
                }],
                globalCoord: true
              },
              
              shadowColor: "#092f8f", // 阴影-外发光
              shadowBlur: 5,  //模糊级数
              shadowOffsetX: 5,  //偏移量
              shadowOffsetY: 6,
            },
          },
          regions: [{
            name: '南海诸岛',
            itemStyle: {
              opacity: 0.5
            }
          }] 
        },

        series: [{
          //配置地图相关参数,绘制地图,这个对象是关于地图图表的相关设置
          type: 'map',
          label: {
            normal: {
              show: true,  //常态下是否显示省份名称
              textStyle: {
                color: '#000000'
              }
            },
            emphasis: {
              textStyle: {
                color: '#FFFFFF',
                fontSize: 15
              }
            }
          },
          zoom: 1.1,
          map: 'china',
          itemStyle: {
            normal: {
              // borderWidth:1,
              areaColor: "#8076a3",
              areaColor: {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.8,
                colorStops: [{
                  offset: 0,
                  color: '#e2e1e4'    //0%处颜色
                }, 
                {
                  offset: 1,
                  color: '#8076a3'    //100%处颜色
                }],
                globalCoord: true
              }
            },
            emphasis: {
              borderWidth: 2,
              borderColor: "#fff",
              areaColor: "#ed5a65", //悬浮区背景
            }
          },
        },

        //地图中散点图配置
        {
          type: 'effectScatter',
          coordinateSystem: 'geo',
          showEffectOn: 'render',
          symbolSize: 10,   //散点大小
          zlevel: 1,
          data: this.points,
          // data: [{ value: [118.8062, 31.9208], itemStyle: { color: '#4ab2e5' } }],
          rippleEffect: { //涟漪特效相关配置
          period: 15, //动画的周期，秒数
          scale: 4,  //动画中波纹的最大缩放比例
          brushType: 'fill',  //波纹的绘制方式，可选 'stroke' 和 'fill'
          }

        },

        // 地图中线路动画效果设置
        {
          type: 'lines',
          zlevel: 2,
          data: this.linesData,
          effect: {
            show: true ,
            period: 10 ,
            // delay: 50 ,
            // constantSpeed: 0 ,
            symbol: 'arrow' ,
            symbolSize: 5 ,
            // // color ... ,
            trailLength: 0.2 ,
            // loop: true ,
            // roundTrip: false ,
          },
          lineStyle:{
            type: 'dotted',
            curveness: 0.2,   //  曲率
            width: 1,
            opacity: 0.1,   //透明度
          },
          
        }
        
        ]
      }
      myChart.setOption(option)
    }
  },

  mounted() {
    
  },

  
}
</script>

<style lang="scss" scoped>
.travel-map {
  width: 100%;

  #main{
    width: 100%;
    height: 500px;
    // background-color: rgba(127, 255, 212, 0.203);
  }
}
</style>